.header {
  display: flex;
  justify-content: center;
  /*background-image: url(../src/images/navbar.jpeg);*/
  /*background-color: #EAE7DC;*/
  /*background-color: inherit;*/
  background: rgb(72,69,108);
  background: linear-gradient(90deg, rgba(72,69,108,1) 0%, rgba(140,1,128,0.989233193277311) 49%, rgba(101,139,147,1) 100%);
}

.header_link {
  padding: 20px;
  color: white;
  font-weight: bold;
  background-color: transparent;
  border: none;
  border-bottom: 3px solid transparent;
  cursor: pointer;
  outline: none;
}

.selected {
  border-bottom: 3px solid #000000;
  color: #000000;
}


.section {
  /*height: 100vh;*/
}

.sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.contactStyle {
  background: rgb(72,69,108);
  background: linear-gradient(90deg, rgba(72,69,108,1) 0%, rgba(255,1,232,0.989233193277311) 49%, rgba(101,139,147,1) 100%);
    height: 120vh;
  }

  .cont{
    
      padding-left: 42%;
