    color: white;
    
  }
  .formField{
    display: inline-block;
    align-items: center;
    padding-left:33%;
    padding-top: 3%;
    width:100%;
    
  }
  input[type=text], select {
    width: 50%;
    padding-left: 20%;
    padding: 16px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  input[type=submit] {
    width: 100%;
